.nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(178, 178, 230, 0.76);
    

}
.nav a{
    text-decoration: none;
    color: black;
}
.logo{
    font-size: 2rem;
}
.menu{
    display: flex;
    align-items: center;
}
.list{
    display: flex;
    list-style: none;
    margin: 10px 0 ;
    font-size: 1.5rem;
}
.list li{
    margin: 0 5px;
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 5px;
    transition: background-color .3s;
}
.list li:hover{
    background-color: aliceblue;
}
.list a{
    display: block;
    width: 100%;
    height: 100%;
}
.button{
    display: flex;
    margin-left: 20px;
}
.button div{
    margin: 0 5px;
    font-size: .9rem;
    cursor: pointer;
    border-radius: 50%;
    text-align: center;
    line-height: 25px;
    width: 25px;
    height: 25px;
    transition: background-color .3s linear;
}
.buttonActive{
    background-color:rgba(240, 248, 255, 0.801);
}