body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
.container{
  padding: 0 15%;
}
.textContainer{
  padding: 50px 100px;
  background-color: antiquewhite;
  height: 150px;
}
.text{
  font-size: 1.2rem;
  line-height: 30px;
}
.brflag{
  position: relative;
  margin: 10px auto;
  width: 400px;
  height: 200px;
  background-color: rgb(6, 175, 6);
  display: flex;
  justify-content: center;
  align-items: center;
}
.brflag::before{
  content: '';
  position: absolute;
  width: 130px;
  height: 130px;
  background-color: yellow;
  transform: rotate(45deg);
}
.brflag::after{
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  background-color: blue;
  border-radius: 50%;
}
.vzflag{
  position: relative;
  margin: 10px auto;
  width: 400px;
  height: 200px;
  background-color: yellow;
}
.vzflag::before{
  content: '';
  position: absolute;
  width: 100%;
  height: 33.33%;
  background-color: blue;
  transform: translateY(100%);
}
.vzflag::after{
  content: '';
  position: absolute;
  width: 100%;
  height: 33.33%;
  background-color: red;
  transform: translateY(200%);
}


